
.mainContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 100%;

  .container {
    background-color: rgba(222, 223, 255, 0.58);
    height: 70%;
    width: 70%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    // height media queries

    @media screen and (min-height: 768px) {
      height: 537px;
    }

    @media screen and (max-height: 660px) {
      height: 90%;
    }

    @media screen and (max-height: 660px) {
      height: 80%;
    }

    @media screen and (max-height: 570px) {
      height: 500px;
    }

    // width media queries

    @media screen and (min-width: 800px) {
      width: 537px;
    }

    @media screen and (max-width: 600px) {
      width: 80%;
    }

    @media screen and (max-width: 500px) {
      width: 87.5%;
    }

    @media screen and (max-width: 400px) {
      width: 95%;
    }

    .form {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      width: 75%;

      .typography {
        text-align: center;
      }

      .descriptionForm {
        padding-top: 5px;
        text-align: center;
      }

      .formGroup {
        width: 100%;
        padding-bottom: 20px;
      }

      .textField {
        padding-bottom: 20px;
      }

      .signUp {
        padding-top: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      .text {
        text-align: right;

        &:hover {
          color: #4f16d2;
        }
      }
    }
  }
}